<template>
  <div class="page home-page">
    <w-header>
      <span class="logo">W</span>
      <button @click="showNav"><i class="fas fa-bars fa-2x"></i></button>
    </w-header>
    <div class="home-layout">
      <div class="image-ripples">
        <img
          src="../assets/female.png"
          alt="female"
          v-if="thisUser.details.gender === 'female'"
        />
        <img
          src="../assets/male.png"
          alt="male"
          v-if="thisUser.details.gender === 'male'"
        />
        <div v-if="chatSearching"></div>
      </div>
      <button @click="matchUser(true)" v-if="!chatSearching">New Chat</button>
      <div class="ad-space" v-if="!chatSearching">
        <a href="https://www.facebook.com/Moneerprofacc/">
          <img src="../assets/my-ad.jpg" alt="ad" />
        </a>
      </div>
      <button
        class="stop-searching"
        @click="matchUser(false)"
        v-if="chatSearching"
      >
        <i class="fas fa-times fa-2x"></i>
      </button>
      <div v-if="chatSearching" class="hints">
        <i class="fas fa-circle"></i>
        <span>{{ connected }} people are connected</span>
        <br />
        <span class="tip"
          >Tip: If it is taking so long, try reducing your filters</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import WHeader from "../components/WHeader.vue";
export default {
  name: "Home",
  data() {
    return {
      chatSearching: false,
      socket: this.$store.state.socket,
    };
  },
  components: { WHeader },
  computed: {
    thisUser() {
      return this.$store.state.user;
    },
    theEventsPreventingCounter() {
      return this.$store.state.homePageVisits;
    },
    connected() {
      return this.$store.state.connected;
    },
  },
  methods: {
    showNav() {
      this.$store.commit("toggleNav");
    },

    matchUser(decision) {
      // set the searching state and connect to socket io
      this.chatSearching = decision;
      if (decision) {
        // if the user is searching emit the matchThisOne event to server with this user data
        this.socket.emit("matchThisOne", this.thisUser);
      } else {
        this.socket.emit("leaveRoom", {
          roomId: this.thisUser.room,
          isPartner: false,
        });
      }
    },
  },
  mounted() {
    this.$store.commit("defaultPartner");
  },
};
</script>

